export default {
  methods: {
    gotoCreate () {
      this.$store.commit('SET_RETURN_TEXT', '新建课程')
      this.$store.commit('SET_RETURN_URL', this.$route.path)
      this.$router.push({ path: '/createCourse' })
    }
  }
}



