// import request from '../utils/request'
import star from '../components/star'
export default {
  name: 'feedbackDiag',
  components: { star },
  data () {
    return {
      score: 0,
      feedText: ''
    }
  },
  props: {
    show: Boolean,
    item: Object,
    userType: String
  },
  methods: {
    updateScore (score, item) {
      this.score = score
      console.log('sate', score, item)
    },
    sendFeed () {
      // request({
      //   url: 'http://www.zhiheyikaoqin.cn/stu/feedback?courserId=1&message=好的&score=5&subId=6,
      //   method: 'get'
      // }).then(res => {
      setTimeout(() => {
        const res = {
          code: 0
        }
        if (Number(res.code) === 0) {
          this.$emit('updateFeed', this.item, this.score, this.feedText)
        }
      }, 20)
    },
    closeFeed () {
      console.log('diagclo')
      this.feedText = ''
      this.score = 0
      this.$emit('closeFeed')
    }
  }
}



