import { errorMsg } from '../utils/common'
import request from '../utils/request'

import star from '../components/star'
import userInfo from './info'
import feedDiag from './feedDiag'
export default {
  name: 'feedback',
  components: { star, userInfo, feedDiag },
  data () {
    return {
      feedbackList: [],
      menu: [],
      feedDiag: false,
      feedItem: {}
    }
  },
  computed: {
    userType () {
      return this.$store.state.user.userType
    }
  },
  mounted () {
    this.getFeedList()
    if (this.userType === 'student') {
      this.menu = this.stuMenu
    } else {
      this.menu = this.teaMenu
    }
  },
  methods: {
    updateScore (score, item) {
      // console.log(score, item)
      item.score = score
    },
    getFeedList () {
      let url = ''
      let req = {}
      if (this.userType === 'teacher') {
        url = '/tea/findTeaHistoryCourse'
        req = {
          teaCode: 1, // 是 string  教师编号
          page: 1, //  否 int 分页的当前页
          pageSize: 1000 //
        }
      } else {
        url = '/stu/lookhistory'
        req = {
          page: 1, // 否 int 页数
          size: 1000, // 否 int 页面大小
          stucode: 1 //
        }
      }
      console.log(url, req)
      setTimeout(() => {
      // request({
      //   url: url,
      //   method: 'get',
      //   data: req
      // }).then(res => {
      //   console.log(res)
        this.feedbackList = [{
          'subId': 27,
          'stuCode': '1',
          'courseId': 4,
          'subStatus': 1,
          'stuCause': null,
          'createTime': '2019-01-02 21:17:48',
          'updateTime': '2019-01-02 21:17:48',
          'teaCourse': {
            'courseId': 4,
            'teaCode': '2',
            teaName: '赵四',
            'courseName': '深度学习',
            'courseDate': '2018-12-28 16:00:00',
            'courseStartTime': '2018-12-29 01:32:59',
            'courseEndTime': '2018-12-29 01:33:01',
            'courseStatus': 3,
            'courseCause': '',
            'courseLocation': '计算所',
            'courseInteractive': 1,
            'createTime': '2018-12-29 01:33:14',
            'updateTime': '2018-12-29 07:03:13'
          },
          'feedBack': null
        }, {
          'subId': 26,
          'stuCode': '1',
          'courseId': 1,
          'subStatus': 3,
          'stuCause': null,
          'createTime': '2019-01-02 21:15:15',
          'updateTime': '2019-01-02 21:17:03',
          'teaCourse': {
            'courseId': 1,
            'teaCode': '6ss',
            teaName: '赵四',
            'courseName': '数学圆的概念',
            'courseDate': '2018-12-28 16:00:00',
            'courseStartTime': '2018-12-30 14:32:24',
            'courseEndTime': '2018-12-31 14:32:28',
            'courseStatus': 0,
            'courseCause': '',
            'courseLocation': '计算所',
            'courseInteractive': 0,
            'createTime': '2018-12-28 22:38:27',
            'updateTime': '2018-12-28 22:38:27'
          },
          'feedBack': {
            'feedbackId': 1,
            'subId': 6,
            'stuScore': 5,
            'stuFeedback': '好的',
            'teaScore': 5,
            'teaFeedback': '作为学生干就完了',
            'createTime': '2018-12-29 02:07:31',
            'updateTime': '2018-12-29 10:49:02'
          }
        }].map(item => {
          item.teaCourse.time = this.getTime(item.teaCourse.courseStartTime) + ' - ' + this.getTime(item.teaCourse.courseEndTime)
          return item
        })
      }, 0)
    },
    getTime (str) {
      const hour = str.split(' ')[1].split(':')[0]
      const min = str.split(' ')[1].split(':')[1]
      return hour + ':' + min
    },
    sendFeedback (item) {
      if (item.text === '') {
        errorMsg(this, '填写反馈')
        return
      }
      let url = ''
      let req = {}
      if (this.userType === 'teacher') {
        url = '/tea/createFeedBack'
        req = {
          subId: item.subId, // 是 int 预约编号
          teaFeedBack: item.text, // 是 string  反馈内容
          score: item.score // 是 int 教师给学生的打分
        }
      } else {
        url = '/stu/feedBack'
        req = {
          courserId: item.courserId, // 是 int 课程id
          message: item.text, // 是 string  反馈信息
          score: item.score, // 是 int 评分
          subId: item.subId // 是 int 预约表id
        }
      }
      console.log(url, req)
      request({
        url: url,
        method: 'post',
        data: req
      }).then(res => {
        if (Number(res.code) === '0') {
          this.getFeedList()
        }
      })
    },
    updateFeed (item, score, feedText) {
      if (this.userType === 'student') {
        item.feedBack = {
          stuScore: score,
          stuFeedback: feedText
        }
      } else {
        item.feedBack = {
          teaScore: score,
          teaFeedback: feedText
        }
      }
      this.closeFeed()
      console.log('updateFeed', item, score, feedText)
    },
    openFeed (item) {
      this.feedItem = item
      this.feedDiag = true
    },
    closeFeed () {
      this.feedItem = {}
      this.feedDiag = false
    }
  }
}



