import userInfo from './info'
// import { deepClone } from '../utils/common'
export default {
  name: 'bookMenu',
  components: { userInfo },
  data () {
    return {
      stuMenu: [{
        name: '待审核',
        link: '/bookCourse/booking',
        icon: 'icon-kexuejishu-'
      }, {
        name: '预约成功',
        link: '/bookCourse/success',
        icon: 'icon-kexuejishu-'
      }, {
        name: '预约失败',
        link: '/bookCourse/fail',
        icon: 'icon-kexuejishu-'
      }, {
        name: '已结束',
        link: '/bookCourse/end',
        icon: 'icon-kexuejishu-'
      }],
      teaMenu: [{
        name: '待确认',
        link: '/bookCourse/booking',
        icon: 'icon-kexuejishu-'
      }, {
        name: '预约成功',
        link: '/bookCourse/success',
        icon: 'icon-kexuejishu-'
      }, {
        name: '预约失败',
        link: '/bookCourse/fail',
        icon: 'icon-kexuejishu-'
      }, {
        name: '已结束',
        link: '/bookCourse/end',
        icon: 'icon-kexuejishu-'
      }],
      menu: []
    }
  },
  mounted () {
    if (this.$store.state.user.userType === 'student') {
      this.menu = this.stuMenu
    } else {
      this.menu = this.teaMenu
    }
  },
  methods: {
    goto (url) {
      this.$router.push({ path: url })
    },
    gotoAll () {
      this.$router.push({ path: '/bookCourse/all' })
    }
  }
}



